/*start style element*/
body {
	font-family: Rubik, sans-serif;
	/* background-color: #f7dad9; */
	background: #ffafbd; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#ffc3a0,
		#ffafbd
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#ffc3a0,
		#ffafbd
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

footer {
	color: #f1e9e5;
}
/*end*/

/*start custom Clssess */
.main-title {
	color: #5d534a;
}
.fit-content {
	width: fit-content;
}
/*end*/

/*start custom Clssess */
.my-modal-backdrop {
	visibility: visible;
	opacity: 1;
	z-index: 1000;
	transform: scaleY(1);
	transform-origin: top center;
	background-color: rgba(200, 200, 200, 0.85);
	transition: all 0.5s ease-in-out;
}

.my-modal-backdrop.hidden {
	visibility: hidden;
	pointer-events: none;
	transform: scaleY(0);
	z-index: -1;
	opacity: 0;
}

.my-modal__close-icon-container {
	display: block;
	cursor: pointer;
}
/*end*/
